import React, { useState, useEffect, useCallback, useRef } from 'react';

import Home from './home/Home';
import './App.scss';
import { initializeApp } from "firebase/app";
import { getAnalytics, logEvent } from "firebase/analytics";

const App = () => {
  const [hoverable, setHoverable] = useState(true);
  const circle = useRef(null);
  const circle2 = useRef(null);

  const firebaseConfig = {
    apiKey: "AIzaSyCaP92eoq4bEjfQzxZ26azC8IWJ970HiWU",
    authDomain: "portfolio2-47919.firebaseapp.com",
    databaseURL: "https://portfolio2-47919.firebaseio.com",
    projectId: "portfolio2-47919",
    storageBucket: "portfolio2-47919.appspot.com",
    messagingSenderId: "572088866809",
    appId: "1:572088866809:web:cdb5d225cffe14baa90f28",
    measurementId: "G-250B7QK8NR"
  };

  const app = initializeApp(firebaseConfig);
  const analytics = getAnalytics(app);
  logEvent(analytics, 'notification_received');

  const growMouse = useCallback(() => {
    if(circle.current && circle2.current){
      circle.current.style.opacity = 0;
      circle2.current.style.opacity = 1;
      circle.current.style.transform = 'translate(-20px, -20px) scale(1.5)';
      circle2.current.style.transform = 'translate(-12px, -12px) scale(1.5)';
    }
  }, [circle, circle2])

  const shrinkMouse = useCallback(() => {
    if(circle.current && circle2.current){
      circle.current.style.opacity = 1;
      circle2.current.style.opacity = 0;
      circle.current.style.transform = 'translate(-20px,-20px) scale(1)';
      circle2.current.style.transform = 'translate(-12px ,-12px) scale(1)';
    }
  }, [circle, circle2])

  //Mouse Events

  useEffect(() => {
    if(circle.current && circle2.current){
      circle.current.style.opacity = 1;
      circle2.current.style.opacity = 0;
      circle.current.style.transform = 'translate(-20px,-20px) scale(1)';
      circle2.current.style.transform = 'translate(-12px, -12px) scale(1)';
    }
    var arrow = document.getElementById('arrow');
    if(arrow){
      arrow.style.opacity = 100 - (document.documentElement.scrollTop) + "%";
      setHoverable(document.documentElement.scrollTop < 100);
    }
  }, [])

  useEffect(() => {
    var xMousePos = 0;
    var yMousePos = 0;
    const aboutSection = document.getElementsByClassName('about')[0];

    const setIconPos = () => {
      circle2.current.style.left = xMousePos + 'px';
      circle2.current.style.top = yMousePos + '%';
      circle.current.style.left = xMousePos + 'px';
      circle.current.style.top = yMousePos + '%';
    }

    const checkForSection = () => {
      if (aboutSection) {
        if (aboutSection.getBoundingClientRect().top !== 0 && (aboutSection.getBoundingClientRect().top / window.innerHeight * 100) < yMousePos) {
          circle.current.style.borderColor = 'white';
        }
        else {
          circle.current.style.borderColor = 'black';
        }
      }
    }

    const onMove = (e) => {
      xMousePos = e.pageX;
      yMousePos = (e.pageY - document.documentElement.scrollTop) / window.innerHeight * 100;
      if(circle.current && circle2.current){
        setIconPos();
        checkForSection();
      }
    }

    if (!navigator.userAgent.match(/(iPad)|(iPhone)|(iPod)|(android)|(webOS)/i)) {
      document.addEventListener('mousemove', onMove);
      document.addEventListener('scroll', checkForSection);
      var links = document.getElementsByClassName('link');
      var arrow = document.getElementById('arrow');
      for (var link of links) {
        link.addEventListener("mouseenter", growMouse);
        link.addEventListener("mouseout", shrinkMouse);
      }
      if (!hoverable) {
        setTimeout(() => {
          arrow.removeEventListener("mouseenter", growMouse);
          arrow.removeEventListener("mouseout", shrinkMouse);
        }, 200)
      }
    }
  }, [hoverable, shrinkMouse, growMouse])

  return (
    <>
      <div className='circle-container'>
        <i id="circle" ref={circle} />
        <i id="circle2" ref={circle2} />
      </div>
      <div className='app-container'>
        <Home hoverable={hoverable} setHoverable={(v) => setHoverable(v)} />
      </div>
    </>)
};



export default App;
